import { SET_ALERT, REMOVE_ALERT } from "../actions/types";

// Define el estado inicial para Alert
const initialState = { alert: null };

/**
 * Reducer de Alertas que maneja el estado de la alerta.
 *
 * @param {object} state - Estado actual de la alerta.
 * @param {object} action - Accin a ejecutar sobre el estado de la alerta.
 * @returns {object} - Nuevo estado de la alerta.
 */
export default function Alert(state = initialState, action) {
  // Extrae el tipo y el payload de la accin
  const { type, payload } = action;

  // Realiza diferentes acciones en funcin del tipo de la accin
  switch (type) {
    // Si la accin es SET_ALERT, actualiza el estado de alerta
    case SET_ALERT:
      return {
        ...state,
        alert: payload,
      };

    // Si la accin es REMOVE_ALERT, elimina el estado de alerta
    case REMOVE_ALERT:
      return {
        ...state,
        alert: null,
      };

    // Si la accin no coincide con ninguna de las anteriores, devuelve el estado actual
    default:
      return state;
  }
}
